	module.exports = {"data":{"post":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/hiring-a-frontend-developer/index_ru.md absPath of file >>> MarkdownRemark","html":"<p>За 2 года поисков работы в Европе я прошла десятки собеседований. В промежутках собеседовалась и в американские компании, не имея намерения туда уехать, а просто для тренировки.\n<excerpt/></p>\n<p>Везде процесс собеседований устроен примерно одинаково.\nСначала вы находите вакансию на LinkedIn или другом сайте, и откликаетесь на неё, прикладывая резюме и cover letter. Cover letter стоит написать обязательно. Причём не копи-паст, а нормальное письмо о том, чем понравилась именно эта вакансия, исходя из её описания, и почему вы подходите для этой работы. Если в описании вакансии есть имя разместившего её рекрутера, стоит начать письмо, обратившись к нему (или ей). Конверсия повышается в несколько раз.\nЕсли у вас есть агент (это бесплатно, и они сами найдут вас на LinkedIn), то эту часть он сделает за вас. И скорее всего хорошо сделает, так что не отказывайтесь от агентов.</p>\n<p>Первое интервью обычно с HR. Это просто знакомство, где оценивают общую адекватность и владение языком для того, чтобы проходить дальнейшее интервью. Тут же можно задавать вопросы о компании, вакансии, организации переезда, стране. Но про технические особенности может знать не каждый HR. Так что иногда такое интервью совсем короткое, обсуждать нечего, и получается минут 5-10.</p>\n<p>Затем предлагают тестовое задание. По моим ощущениям тестовые задания обычно легче, чем это принято в России. Мне кажется, выпускник <a href=\"https://academy.yandex.ru/events/shri/\">ШРИ</a> (и любой другой специалист соответствующего уровня) способен хорошо выполнить любое европейское тестовое задание.\nЯ начинаю писать перед большим событием в моей жизни — переездом в Голландию. Так что первые записи будут о том, как это получилось, и о подробностях произошедшего.</p>\n<p>По тестовому заданию можно составить предварительное впечатление, о том что за разработчики уже есть в компании. Мне очень хотелось, чтобы средний уровень фронтенда в компании был высокий, а в Европе по нашим русским меркам обычно уровень ниже. Самым сложным и интересным мне показалось тестовое задание от Hyves, который я в итоге и выбрала. И разработчики на собеседовании тоже понравились.</p>\n<p>Если вариант тестового задания работадателю нравится, назначают собеседование с разработчиками по Скайпу или телефону. Причём в Скайпе не всегда включают камеру, получается только звук, а это намного сложнее воспринимать. (Поэтому, кстати, я рекомендую не останавливаться на сериалах в тренировках английского языка, а как можно раньше переходить на подкасты без видео-ряда и радио).\nТакое собеседование обычно занимает 40 минут или час, на нём спрашивают о профессиональном пути вообще, задают технические вопросы, иногда разбирают тестовое задание.\nЕвропейские интервьюеры всегда находят время рассказать о вакансии и ответить на вопросы кандидата, сколько бы их ни было.</p>\n<p>Скайп-собеседований с разработчиками может быть несколько. Это зависит от структуры компании. У некоторых компаний есть американская ветвь, и второе собеседование проводится оттуда, даже если нанимать планируют в Европу.\nТакие собеседования назначаются раз в 5-7 дней. То есть если предстоит 3 собеседования, процесс вполне может занять 3 недели.</p>\n<p>Последнее удаленное собеседование обычно проводит глава технического департамента лично, и оно скорее «за жизнь». Даже если возникают технические вопросы, то такие, которые не прадполагают единственно правильного ответа, а скорее дают возможность объяснить свои предпочтения и позицию.</p>\n<p>Завершающий этап — личное собеседование, для которого компания приглашает в свою страну. Такой перелет и проживание в отеле принято оплачивать со стороны компании. Не все компании могут себе позволить привозить кандидатов на собеседования. Поэтому пункт опциональный. Но если у компании нет бюджета на такие цели, она абсолютно точно не будет настаивать на личном общении за счёт кандитата.\nНа моей памяти были компании, способные сделать оффер по результатам телефонных собеседований. Я так не могу, думаю, что важно лично увидеть людей с которыми будешь работать. Поэтому я определила, что если буду близка к принятию такого офера, то уже после его предложения съезжу за свой счёт лично. Такое решение было у меня по отношению к барселонской вакансии, хотя я так туда и не доехала, выбрав в итоге Амстердам.</p>\n<p>Личное собеседование в компании — это обычно целый сет бесед. То есть 3-4 часовых собеседований с разными разработчиками. Собеседуют 1 на 1 или 2е на 1. Предполагается перерыв на обед, и обедом часто кормят прямо там или в близлежащем кафе. Это тоже своего рода собеседование, про то, что кандидат за человек.</p>\n<p>Результат личных собеседований, он же окончательный результат, сообщают лично в тот же день, или через пару дней.\nВместе с принципиальным согласием взять кандидата на работу даётся оффер, в которм описывюат размер зарплаты, соцпакет, помощь при переезде и прочие подробности.</p>\n<p>Затем, если вы соглашаетесь принять офер, продолжается общение с HR по вопросам оформления визы и переезда. Про это напишу позже.</p>","fields":{"slug":"ru/life/hiring-a-frontend-developer/","prefix":"","disqusIdentifier":"life-hiring-a-frontend-developer-index-ru"},"frontmatter":{"title":"Процесс собеседования разработчика","date":"04 September 2013","v2":true,"old":null,"tumblr":true,"meta":null,"cover":null}},"authornote":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Mr. Gatsby</strong> Proin ornare ligula eu tellus tempus elementum. Aenean <a href=\"/\">bibendum</a> iaculis mi, nec blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus. Blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus.</p>"},"site":{"siteMetadata":{"siteUrl":"http://varya.me/"}}},"pathContext":{"slug":"ru/life/hiring-a-frontend-developer/","prev":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/life-blog/index_ru.md absPath of file >>> MarkdownRemark","fields":{"slug":"ru/life/life-blog/","prefix":"","lang":"ru","disqusIdentifier":"life-life-blog-index-ru"},"frontmatter":{"title":"Персональный блог, теперь standalone"}},"next":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/home-finance/index_ru.md absPath of file >>> MarkdownRemark","fields":{"slug":"ru/life/home-finance/","prefix":"","lang":"ru","disqusIdentifier":"life-home-finance-index-ru"},"frontmatter":{"title":"Как мы копим и тратим свои деньги"}}}}

